  constructor() { }
  getUswerAsk(userMobile){
   const userAsk =  this.userAskArr.filter((ele) =>{
      return ele.mobile === userMobile;
    })

    const question = [];
    userAsk.forEach((ele, index) => {
       question[index] = ele['question'];
    })
    return  question;
  }
